.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2rem;
}

/* style the select stock section */
.select-stock {
  margin-bottom: 2rem;
}

.select-stock h2 {
  margin-bottom: 0.5rem;
}

.select-stock button {
  margin-right: 1rem;
}

.select-stock input {
  margin-left: 1rem;
  padding: 0.5rem;
}

/* style the results section */
.results {
  margin-top: 2rem;
}

.results h2 {
  margin-bottom: 0.5rem;
}

.results ul {
  list-style: none;
  padding-left: 0;
}

.results li {
  margin-bottom: 0.5rem;
}